        data: { "sgc": false, "sfy": false, "qfy": false, "lrc": { "version": 16, "lyric": "[00:00.000] 作曲 : Uru\n[00:00.593] 作词 : 唐恬\n[00:01.779]原曲：Uru《Remember》\n[00:03.779]制作人：郑楠@SBMS\n[00:33.431]花开过 悄然无息\n[00:35.971]蝉声在 夏末静谧\n[00:39.010]每一天 慢慢走过\n[00:42.339]而时光 却匆匆\n[00:45.280]\n[00:45.886]我们是 沿途的旅客\n[00:48.934]相遇多 蜿蜒离奇\n[00:51.733]像涟漪 边靠近 边失踪\n[00:56.054]\n[00:57.271]那些无声的告别 曾存在过吗？\n[01:03.453]我曾这样遇见你 怎么证明呢？\n[01:09.906]曾经同路的背影 以记忆关联 被时间带走\n[01:16.830]像一个一个 追不回的梦\n[01:22.501]\n[01:23.576]随夏日远去的你\n[01:28.582]隐没在回忆里 去时间的尽头\n[01:36.091]我带着失去的拥有 继续人海里漂流\n[01:42.471]告别过 孤单也温柔\n[01:48.181]\n[01:48.989]岁月唱沉默的歌\n[01:53.934]被它抹去的名字 再不能还给我\n[02:01.143]曾经的拥抱 是真的\n[02:04.614]也是真的 回不去了\n[02:07.829]明白 再见啊 永不能再见了\n[02:13.322]消失在时光中\n[02:17.470]\n[02:27.888]生命是 不断的离开\n[02:31.858]又不断 有人到达\n[02:34.227]就算是 善意的谎话\n[02:37.094]也感谢 你来过\n[02:40.765]\n[02:41.221]有一些 话还没讲完\n[02:44.058]有些事 没有答案\n[02:46.952]是多么 完美的遗憾\n[02:51.228]\n[02:52.703]那些无声的告别 曾存在过啊\n[02:58.285]我曾这样遇见你 已足够幸福了\n[03:05.446]故事一幕一幕 消失在风里 被时间带走\n[03:12.023]我空着双手 心却是满的\n[03:17.436]\n[03:18.738]随夏日远去的你\n[03:23.598]隐没在回忆里 消失在那个路口\n[03:30.720]每当我再次 抬起头\n[03:34.177]就连寂寞 也好温柔\n[03:37.230]走散的我们 仰望着同片天空\n[03:43.892]\n[03:44.335]就算夏天 逝去了 花也曾开过\n[03:47.515]在无人的角落\n[03:50.279]时光的 手一挥 快乐难过 不会有人记得\n[03:56.425]我也很高兴 和你遇见过\n[04:02.806]\n[04:06.647]随夏日远去的你\n[04:11.732]隐没在回忆里 去时间的尽头\n[04:18.818]我带着失去的拥有 继续人海里漂流\n[04:25.460]告别过 孤单也温柔\n[04:31.074]\n[04:32.393]岁月唱沉默的歌\n[04:37.497]被抹去的名字 再不能还给我\n[04:44.009]昨日在风中 挥着手\n[04:47.735]舍不得也 要走了\n[04:50.867]那就这样吧 在忘却里自由\n[04:56.537]在瞬间里永久\n[05:00.556]\n[05:04.960]制作助理：王子\n[05:06.217]录音：汝文博@SBMS\n[05:07.633]录音棚：SpeedBumps Music Studio\n[05:09.104]和声&和声编写：余昭源\n[05:10.470]吉他：薛峰\n[05:11.679]贝斯：薛峰\n[05:13.038]弦乐编写：王子 郑楠\n[05:14.331]弦乐监制：李朋\n[05:15.774]弦乐：国际首席爱乐乐团\n[05:17.201]混音：赵靖@SBMS\n[05:18.424]母带：赵靖@SBMS\n[05:19.677]混音母带录音室：SpeedBumps Music Studio\n[05:20.972]原曲：Uru《Remember》\n" }, "klyric": { "version": 0 }, "tlyric": { "version": 0 }, "code": 200 }
