        data: { "sgc": false, "sfy": false, "qfy": false, "lrc": { "version": 13, "lyric": "[00:00.000] 作曲 : Zok\n[00:00.759] 作词 : Zok\n[00:02.277]制作：Zok\n[00:03.533]I don’t wanna call you\n[00:06.794]My feeling for you seems like it just won’t go\n[00:13.069]How can you be so cruel\n[00:16.329]Those teardrops tell me what you used to meant to me\n[00:23.099]And I dreamed about you last night\n[00:25.608]That we are supposed to make that right\n[00:27.878]We were so young\n[00:30.136]We were so dumb\n[00:32.894]If these words never came out\n[00:35.405]Will things be different from now\n[00:37.412]I will be there\n[00:39.920]No more tears\n[00:42.931]So erase my pain\n[00:44.686]Cause all these strives will be vain\n[00:47.192]I don’t care\n[00:49.700]It don’t hurt\n[00:52.707](BUILD UP)\n[01:02.239](MIDTEMPO DROP)\n[01:21.559](BREAK)\n[01:26.326](VERSE2)\n[01:45.394](BUILD UP)\n[02:02.208](VOCAL CHOP FILL)\n[02:04.717](GLITCHHOP DROP)\n" }, "klyric": { "version": 0, "lyric": null }, "tlyric": { "version": 9, "lyric": "[00:02.277]\n[00:03.533]我不想与你联络\n[00:06.794]但你带给我的感觉着实令人难以忘却\n[00:13.069]你怎能如此无情\n[00:16.329]那些泪水提醒着我你曾经对我来说意味着什么\n[00:23.099]昨夜你曾出现在我的梦里\n[00:25.608]我们本该去纠正那些事\n[00:27.878]从前的我们懵懂无知\n[00:30.136]且不善言辞\n[00:32.894]假如这一个个词句未曾脱口而出\n[00:35.405]事情的结果会不会变得不同\n[00:37.412]我想我会留在这里\n[00:39.920]而不再有更多泪水\n[00:42.931]所以请抹去我的伤痛吧\n[00:44.686]因为一切的争取都将会是无力的\n[00:47.192]我毫不在意 (Ironic)\n[00:49.700]这根本不伤人 (Ironic)\n[00:52.707]\n[01:02.239]\n[01:21.559]\n[01:26.326]\n[01:45.394]\n[02:02.208]\n[02:04.717]" }, "code": 200 }
